<template>
	
	<div class="indexContainer">
		<header><span>首页</span></header>
		<div class="container">
			<router-link v-if="Receipt" to="/Receipt">
				<div  class="item">
					<div class="iconfont icon-cangku"></div>
					<div>收货</div>		
				</div>
			</router-link>
			<router-link  v-if="ReceiptNumCollect"  :to="{path:'/ConsignmentIndex',query: {name: 'Receipt'}}">
				<div class="item">
					<div class="iconfont icon-querenshouhuo_fuzhi"></div>
					<div>收货扫码</div>
				</div>
			</router-link>
			<router-link  v-if="putaway"  to="/Putaway">
				<div  class="item">
					<div class="iconfont icon-ccgl-shangjiasaomiao-5"></div>
					<div>上架</div>
				</div>
			</router-link>
			<router-link  v-if="inventoryQuery"  to="/InventoryQuery">
				<div  class="item">
					<div class="iconfont icon-chaxun"></div>
					<div>查询</div>
				</div>
			</router-link>
			<router-link  v-if="Replenishment"  to="/ReplenishIndex">
				<div class="item">
					<div class="iconfont icon-ccgl-buhuoguanli-7"></div>
					<div>补货</div>
				</div>
			</router-link>
			<router-link  v-if="RFInvMove"  to="/RFInvMove">
				<div class="item">
					<div class="iconfont icon-kucunyidong"></div>
					<div>移动</div>
				</div>
			</router-link>
			<router-link  v-if="RFPick"  to="/RFPick">
				<div class="item">
					<div class="iconfont icon-jianhuo"></div>
					<div>拣货</div>
				</div>
			</router-link>
			<router-link  v-if="CycleCount"  to="/CycleCount">
				<div class="item">
					<div class="iconfont icon-pandianxiangqing"></div>
					<div>盘点</div>
				</div>
			</router-link>
			<router-link   v-if="RFOrderShip"  to="/RFOrderShip">
				<div class="item">
					<div class="iconfont icon-fayunshuliang"></div>
					<div>发运</div>
				</div>
			</router-link>
			<router-link  v-if="shipmentNumCollect"  :to="{path:'/ConsignmentIndex',query: {name: 'Consignment'}}">
				<div class="item">
					<div class="iconfont icon-kucunpandian"></div>
					<div>发货扫码</div>
				</div>
			</router-link>
			<router-link  v-if="CheckOrder"   to="/CheckOrder">
				<div  class="item">
					<div class="iconfont icon-chaxun"></div>
					<div>按单复核</div>
				</div>
			</router-link>
			<router-link  v-if="CheckLoad"   to="/CheckLoad">
				<div  class="item">
					<div class="iconfont icon-chaxun"></div>
					<div>按装运复核</div>
				</div>
			</router-link>
			<router-link  v-if="SkuBound"  to="/SkuBound">
				<div class="item">
					<div class="iconfont icon-pandianxiangqing"></div>
					<div>条码绑定</div>
				</div>
			</router-link>
			<div v-if="switch_Server"  @click="switchServer">
				<div class="item">
					<div class="iconfont icon-pandianxiangqing"></div>
					<div>切换服务器</div>
				</div>
			</div>
			<router-link  v-if="SystemInfo" to="/SystemInfo">
				<div class="item">
					<div class="iconfont icon-iconfontxitongxinxiw"></div>
					<div>系统信息</div>
				</div>
			</router-link>
		</div>
		<!-- 切换服务器弹出框 -->
		<div v-if="switchServer_show" class="switch_Server">
			<div  class="switch_content">
				<input type="" name="" ref="switchServer_value" v-model="switchServer_value" placeholder="请输入服务器地址">
				<div><span @click="submit_switchServer">确定</span><span  @click="reset_switchServer">重置</span></div>
				<i  @click="close_switchServer" class="iconfont icon-cuowu"></i>
			</div>
		</div>
		<div v-if="tipsHide"  @touchmove.prevent  class="g_tips">
			<div>
				<span>错误信息</span>
				<span>{{msg}}</span>
				<p  @click='tipsHideen(inputFocus)'>确定</p>
			</div>
		</div>
		<div class="exit"  @click="exit">注销</div>
	</div>
</template>
<script type="text/javascript">

import "../../static/font/iconfont.css"
export default{
	name:"Index",
	data(){
		return{
			tipsHide:false,//提示信息是否出现
			msg:"",//提示信息内容
			Receipt:false,
			ReceiptNumCollect:false,
			putaway:false,
			inventoryQuery:false,
			Replenishment:false,
			RFInvMove:false,
			CycleCount:false,
			RFPick:false,
			RFOrderShip:false,
			shipmentNumCollect:false,
			CheckOrder:false,
			CheckLoad:false,
			SkuBound:false,
			switch_Server:false,//切换服务器
			SystemInfo:false,//系统信息
			limitControlData:[],//权限列表
			switchServer_show:false,//选择服务器弹出框是否显示
			switchServer_value:"",//服务器地址
		}
	},
	created(){

		var that = this;

		var SummaryUrl = this.HOST + "RFAuth.do";

		var sum_Data = {
			UserID : that.$store.state.UserID,//用户名
			WarehouseCode : that.$store.state.WarehouseCode,//仓库名称
			Tenant_Code: that.$store.state.Tenant_Code,//租户代码
		}

		var pageData = ["Receipt","ReceiptNumCollect","putaway","inventoryQuery","Replenishment","RFInvMove","CycleCount","RFPick","RFOrderShip","shipmentNumCollect","CheckOrder","CheckLoad","SkuBound","switch_Server","SystemInfo"];

		if(that.$store.state.limitControlList.length){

			that.limitControlData = that.$store.state.limitControlList;

			for(var i = 0;i < pageData.length ; i++){

				for(var j=0 ; j < that.limitControlData.length;j++){

					if(that.limitControlData[j].module_name.split("/")[2].split(".")[0] == pageData[i]){
						that[pageData[i]] = true;
					}
				}
			}
		}else{

			this.httpRequest(SummaryUrl,sum_Data,function(res){

				that.limitControlData = res.data.data;

				that.$store.state.limitControlList = res.data.data;

				for(var i = 0;i < pageData.length ; i++){

					for(var j=0 ; j < that.limitControlData.length;j++){

						if(that.limitControlData[j].module_name.split("/")[2].split(".")[0] == pageData[i]){
							that[pageData[i]] = true;
						}
					}
				}
			})
		}
	},
	methods:{
		// 切换服务器弹出框出现
		switchServer(){
			//弹出框出现
			this.switchServer_show = true;
		},
		//切换服务器  确定按钮
		submit_switchServer(){

			if(!this.switchServer_value){

				this.msg = "请填写服务器地址";

				this.inputFocus = "switchServer_value";

				this.tipsShow("switchServer_value");

				return;
			}

			this.$store.state.Server_Address = this.switchServer_value;

			//设置cookie
			this.setCookie_Server(this.switchServer_value);
			this.KeepServerAddressDefault(this.switchServer_value);

			//跳转到登录页面
			this.$router.push({
				name:"Login",
				query:{
				}
			})
		},
		//切换服务器  重置按钮
		reset_switchServer(){

			var that = this;

			this.$store.state.Server_Address = this.$store.state.Stationary_Server_Address;

			//设置cookie
			this.setCookie_Server(that.$store.state.Stationary_Server_Address);
			this.KeepServerAddressDefault(that.$store.state.Stationary_Server_Address);

			//跳转到登录页面
			this.$router.push({
				name:"Login",
				query:{
				}
			})
		},
		// 关闭切换服务器弹出框
		close_switchServer(){
			//弹出框关闭
			this.switchServer_show = false;
		},
		// 退出登录
		exit:function(){

			// 仓库
			this.$store.state.WarehouseCode = "";

			// 用户名
			this.$store.state.UserID = "";

			// 权限列表
			this.$store.state.limitControlList = [];

			//跳转到登录页面
			this.$router.push({
				name:"Login",
				query:{
				}
			})
		}
	}
}
</script>
<style  scoped lang="less">
	.indexContainer{
		width:100%;
		height: 100%;
		.container{
			margin: 150/50rem auto 0;
			height: 100%;
			width: 500/50rem;
			overflow: hidden;
			.item{
				border-radius:15/50rem;
				float: left;
				width: 200/50rem;
				box-sizing: border-box;
				text-align:center;
				border: 1/50rem solid #ddd;
				padding: 20/50rem 0;
				margin-bottom: 50/50rem;
				margin-right: 100/50rem;
				font-size: 32/50rem;
				color:#000;
				.iconfont{
					font-size: 58/50rem;
					margin-bottom: 10/50rem;
					color:#2B5FD8;
				}
			}
			&>a:nth-child(2) .item,&>a:nth-child(4) .item,&>a:nth-child(6) .item,&>a:nth-child(8) .item,&>a:nth-child(10) .item,&>a:nth-child(12) .item,&>a:nth-child(14) .item{
				margin-right: 0;
			}
			&>div .item{
				margin-right: 0;
			}

		}
		.exit{
			width:600/50rem;
			height: 70/50rem;
			background-color: #285DCE;
			text-align: center;
			line-height: 70/50rem;
			border-radius: 10/50rem;
			color: #fff;
			font-size: 29/50rem;
			margin: 30/50rem auto;
		}
		.switch_Server{
			width: 100%;
			height: 100%;
			position: fixed;
			top: 0;
			left: 0;
			background-color: rgba(0,0,0,0.6);
			font-size: 30/50rem;
			.switch_content{
				width: 600/50rem;
				height: 300/50rem;
				position: fixed;
				top: 50%;
				left: 50%;
				border-radius: 8/50rem;
				margin-top: -150/50rem;
				margin-left: -300/50rem;
				background-color: #fff;
				input{
					margin:60/50rem;
					height: 70/50rem;
					width: 500/50rem;
					padding: 0 10/50rem;
					box-sizing: border-box;
				}
				&>div{
					display: flex;
					span{
						flex-grow:1;
						text-align: center;
						height: 60/50rem;
						line-height: 60/50rem;
						background-color: #2B5FD8;
						color: #fff;
						margin-left: 20/50rem;
					}
					span:nth-child(2){
						margin-right: 20/50rem;
					}
				}
				&>i{
					position: absolute;
					top: -10/50rem;
					right: -10/50rem;
					font-size: 50/50rem;
					z-index: 33;
				}
			}
		}
	}

	@media screen and (max-width: 500px) and (min-width: 479px) {	
		.indexContainer{
			.container{
				.item{
					border-radius:15/50rem;
					width: 185/50rem;
					padding: 20/50rem 0;
					margin-bottom: 30/50rem;
					margin-right: 130/50rem;
					font-size: 26/50rem;
					color:#000;
					.iconfont{
						font-size: 40/50rem;
						margin-bottom: 0;
						color:#2B5FD8;
					}
				}
			}
		}
	}
	@media screen and (max-width: 374px) {	
		.indexContainer{
			.container{
				.item{
					border: 3/50rem solid #ddd;
				}
			}
		}
	}

	@media screen and (max-width: 340px) and (min-width: 251px) {
		.indexContainer{
			.container{
				width: 100%;
				margin: 90/50rem auto 0;
				.item{
					width: 50%;
					border-radius:0;
					padding: 13/50rem 0;
					font-size: 35/50rem;
					margin: 0;
					border:none;
					border-bottom: 3/50rem solid #ddd;
					border-right:  3/50rem solid #ddd;
					color:#000;
					.iconfont{
						font-size: 50/50rem;
						margin-bottom: 0;
						color:#2B5FD8;
					}
				}
				&>a:nth-child(2) .item,&>a:nth-child(4) .item,&>a:nth-child(6) .item,&>a:nth-child(8) .item,&>a:nth-child(10) .item{
					border-right: none;
				}
			}
			.exit{
				height: 80/50rem;
				line-height: 80/50rem;
				font-size: 40/50rem;
				margin-top: 60/50rem;
			}
		}
	}
	@media screen and (max-width: 250px){
		.indexContainer{
			.container{
				margin: 108/50rem auto 0;
				.item{
					border-radius:0;
					margin-bottom:13/50rem;
					padding: 15/50rem 0;
					font-size: 45/50rem;
					color:#000;
					.iconfont{
						font-size: 60/50rem;
						margin-bottom: 10/50rem;
						color:#2B5FD8;
					}
				}
			}
			.exit{
				font-size: 45/50rem;
			}
		}
	}
</style>


